a,abbr,acronym,address,applet,article,aside,audio,b,big,blockquote,body,canvas,caption,center,cite,code,dd,del,details,dfn,div,dl,dt,em,embed,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,html,i,iframe,img,ins,kbd,label,legend,li,mark,menu,nav,object,ol,output,p,pre,q,ruby,s,samp,section,small,span,strike,strong,sub,summary,sup,table,tbody,td,tfoot,th,thead,time,tr,tt,u,ul,var,video{margin:0;padding:0;border:0;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:after,blockquote:before,q:after,q:before{content:'';content:none}table{border-collapse:collapse;border-spacing:0}html{font:62.5% 'Open Sans', arial, sans-serif}body{line-height:1.6;background-color:#233142}h1{margin:20px 0;text-align:center;font-size:45px;color:#FFF}.container{margin:0 auto;padding:0 15px;max-width:800px;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;box-sizing:border-box}.header{margin:10px 0;font-size:20px;color:#e4e4e4}.content{width:100%;max-width:800px;padding:25px;font-size:14px;color:#000;background:#ffffff;border-radius:10px;box-sizing:border-box}.updateBtn{cursor:pointer;margin-top:20px;padding:10px 25px;border:none;font-size:14px;color:#FFF;background:#1eac55;border-radius:5px}.updateBtn:hover{background:#1a9147;box-shadow:0px 0px 8px 8px rgba(0,0,0,0.25)}.updateDesc{margin-top:5px;color:#FFF;font-style:italic}.loader{display:inline-block}.loader:after{content:" ";display:block;width:20px;height:20px;margin:1px;border-radius:50%;border:2px solid #c42c2c;border-color:#2986ff transparent #2986ff transparent;animation:loader 1.2s linear infinite}@keyframes loader{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}
